.lineup{
    background-color: $rosa;
    padding: $separacion 0;

    h3,p{
text-align: center;
color: $blanco;
    }
   .dia {
       text-transform: uppercase;
       margin: $separacion 0;
   }
}

.escenario-contenedor{
  @include tablet{
      @include grid(2, $separacion);
  } 
  .escenario{
    padding-top: $separacion / 2;
  }

  .nombre-escenario{
    color: $negro;
    font-size: 2rem;
    font-weight: $delgada;

    &:after{
content: "";
width: 20rem;
height: 10rem;
display: block;

margin: 2rem auto 0 auto;
background-size: 15rem;//permite controlar el tamaño de una imagen
background-repeat: no-repeat; // COn esto no se repetira 
background-position:center center;
}
  } 

}
.edm .nombre-escenario{
&:after{
    background-image: url(../img/icono_edm.png);
   }
}

.rock .nombre-escenario{
    &:after{
        background-image: url(../img/icono_rock.png);
       }
}
.calendario{
  padding: 0;//Para quitar la franja verde
  list-style: none; //Quita las viñetas
  li{
    padding: $separacion / 2;
    font-weight: $bold;

    span{
      font-weight: $regular;
    }
  }
}
.bg-verde .calendario li:nth-child(odd){
  background-color: darken($verde, 10);
}//Darken es una función de sass y le aplica intensidad de negro

.bg-amarillo .calendario li:nth-child(odd){
  background-color: darken($amarillo, 15);
}//Darken es una función de sass y le aplica intensidad de negro
