.galeria{
    padding: $separacion 0;
    h3{
        text-align: center;
    }
}

.galeria-imagenes{
 padding: 0;
 list-style: none;
margin-top: $separacion;
@include grid(2, $separacion);
@include tablet {
    @include grid(4, $separacion / 2); //Lo dividira en 4 y 3
}
@include desktop {
    @include grid(6, $separacion / 2);//Lo dividira en 2 y 6
 }
}

.overlay{
    position:fixed;// Esto hace que la posicion se vuelva fija
    background-color: $overlay;
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
}

.btn-cerrar{
    width: 5rem;
    height: 5rem;
    background-color: $blanco;
    display: grid; //para centrar
    place-items: center;
    font-weight: $negro;
    font-size: 3rem;
    border-radius: 50%;

    &:hover{
      cursor: pointer;  
    }
}