.header {
    background-color: $verde;
    .contenido-header {
        padding: $separacion / 2 0;
        //Tenia la version "sass": "^1.37.5" y lo cambie a "sass": "~1.32.6" para que me agarrara el 
        //padding $separacion / 2 0; Este modo ya esta absoleto
 
        @include tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
 
    }
 
 h1 {
    color: $blanco;
    margin-bottom: 1rem;
    text-align: center;
    @include tablet {
       margin-bottom: 0;
    }
    &:hover{
       color: $gris;
       
    }
 }
 &.fijo{
position: fixed;
width: 100%;
box-shadow: 0 .3rem .4rem #444; //Añade la sombra
 }
 }
 
 .navegacion-principal{
    display: flex;
    gap: 2rem;
    justify-content: center;
    a {
       color: $blanco;
       font-size: 2rem;
       &:hover{ //Ese ampersand  es la a
       color: $amarillo;
    }
  }  
  
 }